@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

body {
  line-height: 1.5;
  font-family: 'Poppins', sans-serif;
}
html {
  scroll-behavior: smooth;
}

* {
  padding: 0px;
  margin: auto 0;
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: white;
  margin-top: auto;
}

#link-card-shopping {
  color: black;
}

/* nav section */

.nav-bar {
  display: flex;
  font-size: 0.6rem;
  list-style: none;
  background-color: black;
  height: 6vh;
  width: 100%;
  position: relative;
  justify-content: center;
  z-index: 5;
}

.nav-bar-shopping {
  display: flex;
  font-size: 0.7rem;
  list-style: none;
  background-color: #bf2c37;
  height: 8vh;
  width: 100%;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 5;
}

#logo-shopping {
  height: 40px;
  margin-left: 20px;
}

.nav-bar-loja-menu {
  display: flex;
  height: 80%;
  justify-content: space-between;
  align-items: center;
}

.nav-bar-loja-menu > a {
  margin: 0 3rem;
  text-transform: uppercase;
}

.login {
  margin-right: 16px;
  font-size: 0.8rem;
}

.login > * {
  padding: 0.3vh;
  height: 1vh;
}

.login :first-child {
  margin-right: 1rem;
}

.login > a {
  border-radius: 10%;
}

#logo-nav {
  margin-left: 0rem;
}
/* fim navbar */

/* part-2 */
.cta {
  width: 100%;
  height: 86vh;
  position: relative;
  /* background: url(./Imagens/Shoppings/São_José_dos_Campos/Vale_Sul/Capa_Vale_Sul.jpg) no-repeat center center/cover; */
  display: flex;
}

.cta::after {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: inset 60px 70px 250px #000000, inset -60px -10px 50px #000000;
}

.cta-content {
  position: relative;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  z-index: 2;
  margin: auto;
}

@media (min-width: 137px) {
  #shopping {
    font-weight: 300;
    font-size: 50px;
  }

  #subtitulo-shop {
    margin-top: 1em;
    font-weight: 300;
    font-size: 10px;
    font-size: 2vh;
  }
}

@media (max-width: 136px) {
  #shopping {
    font-weight: 300;
    font-size: 100px;
  }

  #subtitulo-shop {
    margin-top: 1em;
    font-weight: 300;
    font-size: 10px;
    font-size: 2vh;
  }
}

.cta-content-search {
  margin-top: 4vh;
}

.btn-medium {
  border: 2px solid rgba(25, 25, 25, 0.6);
  border-radius: 4px;
  background: red;
  font-style: normal;
  font-weight: bold;
  font-size: 0.6rem;
  font-size: 2vh;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  color: #ffffff;
  padding: 0.2rem;
  cursor: pointer;
  outline: none;
  margin-left: 0.5rem;
  width: 10rem;
  width: 30vh;
}

.btn-medium:hover {
  background: rgb(196, 0, 0);
}

#linha-cta {
  height: 8vh;
  margin-top: auto;
  margin-bottom: 0;
  z-index: 3;
  opacity: 0;
  position: absolute;
  bottom: 0vh; /*Chutando o valor para posicionar a linha*/
  background-color: blue;
}

/* titulo e ver mais */

.titulo-e-ver-mais {
  display: block;
  margin-top: 0rem;
}

.titulo-secao {
  margin-top: 0rem;
  font-size: 2rem;
  margin-left: 0rem;
  display: inline-block;
  color: black;
}

.ver-mais {
  margin-top: 0rem;
  font-size: 0.66rem;
  margin-left: 0rem;
  display: inline-block;
  color: black;
  margin-left: 2vh;
}
#container-conteudo {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0px;
}

/* adicional marca */

.marca {
  border-radius: 4%;
  height: 12.8rem;
  width: 10.3rem;
  background-color: white;
  border: 2px solid #c4c4c4;
  align-items: center;
  justify-items: center;
  filter: none;
  -webkit-filter: none;
  margin: 1.5vh 1vw;
}

.img-marca {
  border-radius: 4%;
  height: 12.5rem;
  width: 10rem;
}

.previa-video {
  border-radius: 4%;
  height: 12.5rem;
  width: 10rem;
}

/* secao lista de shoppings */

.titulo-secao {
  margin-top: 3rem;
  font-size: 2rem;
  margin-left: 0rem;
}

.lista-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2vh;
}

@media (max-width: 500px) {
  .lista-cards {
    flex-wrap: wrap;
  }
}

/* Cards individuais */

.card-shopping {
  border: 1px solid #c4c4c4;
  align-items: center;
  width: 10.3rem;
  border-radius: 4px;
  text-align: center;
  height: 13rem;
  transition: all 0.4s ease-in-out;
  margin: 1vw;
  cursor: pointer;
  box-shadow: 28px 10px 88px -45px rgba(0, 0, 0, 0.54);
  -webkit-box-shadow: 28px 10px 88px -45px;
  -moz-box-shadow: 28px 10px 88px -45px rgba(0, 0, 0, 0.54);
  box-shadow: 28px 10px 88px -45px rgba(0, 0, 0, 0.54);
}

.card-shopping:hover {
  transform: scale(1.1);
}

@media (max-width: 750px) {
  .card-shopping {
    transform: scale(0.8);
    margin: 0.1vw;
  }

  .card-shopping:hover {
    transform: scale(0.9);
  }
}

@media (max-width: 550px) {
  .card-shopping {
    transform: scale(0.7);
    margin: 0.1vw;
  }

  .card-shopping:hover {
    transform: scale(0.8);
  }
}

.card-shopping > * {
  display: inline-block;
  align-items: center;
}

.card-shopping p {
  font-size: 0.9rem;
  line-height: 6vh;
}

.img-lista-shoppings {
  height: 9rem;
  border-radius: 2px;
  width: 10rem;
  margin-top: 2px;
}

.story-lista-shoppings {
  position: relative;
  top: 1.5rem;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  height: 3rem;
  display: block;
  border: 2px solid rgba(25, 25, 25, 0.6);
  width: 3rem;
  height: 3rem;
}

.story-lista-shoppings > img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.linha-entre-secoes {
  margin-top: 7vh;
  margin-bottom: 6vh;
  width: 100%;
}

/* secao vitrines */
.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 4vh;
}

.grid-item {
  background-color: silver;
  height: 30vh;
  margin: 0.3vh;
  display: flex;
  cursor: pointer;
  position: relative;
}

.grid-item > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.grid-1 {
  grid-column: 1/5;
}

.grid-2 {
  grid-column: 1/3;
}

.grid-3 {
  grid-column: 3/4;
}

.grid-4 {
  grid-column: 4/5;
}

.grid-5 {
  grid-column: 1/2;
}

.grid-6 {
  grid-column: 2/3;
}

.grid-7 {
  grid-column: 3/5;
}

@media (max-width: 750px) {
  .grid-container {
    display: block;
  }

  .grid-item {
  }
}

.label {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 20%;
  display: flex;
  padding-left: 1rem;
  position: relative;
  top: 40%;
  color: white;
}

.footer-bar {
  background-color: #bf2c37;
  padding: 40px 0;
  box-sizing: border-box;
  margin-top: 60px;
}

.footer-container {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
}

#footer-ul {
  list-style: none;
}

.footer-row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
.footer-col {
  width: 25%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  top: 0;
}

#footer-col-eshop {
  width: 15%;
  border-right: 5px solid #ffffff;
}

#footer-col-info-shopping ul {
  margin-top: 63px;
}

.footer-col h4 {
  font-size: 18px;
  color: #ffffff;
  text-transform: capitalize;
  margin-bottom: 35px;
  font-weight: 500;
  position: relative;
}

.footer-col h4::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px;
  background-color: rgba(255, 255, 255, 0.2);
  height: 2px;
  box-sizing: border-box;
  width: 50px;
}

.footer-col ul li {
  margin-bottom: 10 px;
  color: #ffffff;
  font-size: 16px;
  text-transform: capitalize;
  text-decoration: none;
  font-weight: 300;
  display: block;
  transition: all 0.3s ease;
}

.footer-col ul li a {
  font-size: 16px;
  text-transform: capitalize;
  text-decoration: none;
  font-weight: 300;
  display: block;
  transition: all 0.3s ease;
}

.footer-col ul li a:hover {
  color: #ffffff;
  padding-left: 8px;
}

.footer-col .social-links a {
  display: inline-block;
  height: 40px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: 20px 10px 10px 0;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  color: #ffffff;
  transition: all 0.5s ease;
}

.footer-col .social-links a:hover {
  color: #24262b;
  background-color: #ffffff;
}

/* footer responsivo */

@media (max-width: 767px) {
  .footer-col {
    width: 50%;
    margin-bottom: 30px;
  }
}

@media (max-width: 574px) {
  .footer-col {
    width: 100%;
  }
  #footer-col-eshop {
    width: 100%;
  }
  #footer-col-info-shopping {
    margin-top: 0;
  }
}
